import React from "react"
import { connect } from "react-redux"
import { updateChart } from "../../state/actionTypes"
import Button from "./Button"
import styles from "../../styles/budget/chart.module.scss"

import {
  Label,
  Legend,
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ReferenceLine,
  ReferenceArea,
  ResponsiveContainer,
} from "recharts"

const getAxisYDomain = (from, to, ref, offset, data) => {
  const refData = data.slice(from - 1, to)
  let [bottom, top] = [refData[0][ref], refData[0][ref]]
  refData.forEach(d => {
    if (d[ref] > top) top = d[ref]
    if (d[ref] < bottom) bottom = d[ref]
  })

  return [(bottom | 0) - offset, (top | 0) + offset]
}

const colours = [
  "#34558b",
  "#fc766a",
  "#6b5b95",
  "#92b558",
  "#f8cdcd",
  "#95aad3",
  "#964f4c",
  "#ad5e99",
  "#00997b",
  "#e34f33",
  "#d85a7b",
  "#4db6ad",
  "#ebbf57",
  "#6364a5",
  "#9d363f",
  "#decebb",
  "#5bb3b0",
  "#e2654d",
  "#7fc9cb",
  "#bd3645",
  "#c34e7c",
  "#98b2d1",
]

const Chart = ({
  type,
  index,
  leftOfChart,
  rightOfChart,
  refAreaLeftOfChart,
  refAreaRightOfChart,
  topOfChart,
  bottomOfChart,
  animationOfChart,
  newChart,
  details,
  accounts,
  expenses,
  groupings,
}) => {
  let data = []
  let names
  let targets

  switch (type) {
    case "accounts":
      names = accounts[0]
      targets = accounts[1]
      data = accounts.slice(2).map((monthArr, key1) => {
        let object = { month: key1 }
        monthArr.forEach((value, key2) => {
          object = { ...object, [names[key2]]: value }
        })
        return object
      })
      break
    case "expenses":
      names = expenses[0]
      data = expenses.slice(1).map((monthArr, key1) => {
        let object = { month: key1 }
        monthArr.forEach((value, key2) => {
          object = { ...object, [names[key2]]: value }
        })
        return object
      })
      break
    default:
      names = groupings[0]
      data = groupings.slice(1).map((monthArr, key1) => {
        let object = { month: key1 }
        monthArr.forEach((value, key2) => {
          object = { ...object, [names[key2]]: value }
        })
        return object
      })
  }

  let left = leftOfChart[index]
  let right = rightOfChart[index]
  let refAreaLeft = refAreaLeftOfChart[index]
  let refAreaRight = refAreaRightOfChart[index]
  let top = topOfChart[index]
  let bottom = bottomOfChart[index]
  let animation = animationOfChart[index]

  const zoom = () => {
    if (refAreaLeft === refAreaRight || refAreaRight === "") {
      newChart(index, left, right, "", "", top, bottom, animation)
      return
    }

    // xAxis domain
    if (refAreaLeft > refAreaRight)
      [refAreaLeft, refAreaRight] = [refAreaRight, refAreaLeft]

    const focus = Math.floor(Math.random() * names.length)

    // yAxis domain
    const [newBottom, newTop] = getAxisYDomain(
      refAreaLeft,
      refAreaRight,
      names[focus],
      1,
      data
    )

    bottom = newBottom
    top = newTop

    newChart(index, refAreaLeft, refAreaRight, "", "", top, bottom, animation)
  }

  const zoomOut = () => {
    newChart(
      index,
      "dataMin",
      "dataMax + 1",
      "",
      "",
      "dataMax + 1000",
      "dataMin - 1000",
      animation
    )
  }

  const handleOnMouseDown = e => {
    newChart(
      index,
      left,
      right,
      e.activeLabel,
      refAreaRight,
      top,
      bottom,
      animation
    )
  }

  const handleOnMouseMove = e => {
    refAreaLeft &&
      newChart(
        index,
        left,
        right,
        refAreaLeft,
        e.activeLabel,
        top,
        bottom,
        animation
      )
  }

  return (
    <figure className={styles.figure}>
      <div
        className="highlight-bar-charts"
        style={{ userSelect: "none", width: "100%" }}
      >
        <Button
          className="btn update"
          label="🔎: Zoom Out"
          colour="blue"
          handleClick={zoomOut}
        />
        <ResponsiveContainer width="100%" height={400}>
          <LineChart
            data={data}
            onMouseDown={handleOnMouseDown}
            onMouseMove={handleOnMouseMove}
            onMouseUp={zoom}
            margin={{ top: 15, right: 30, left: 20, bottom: 5 }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis
              xAxisId="0"
              allowDataOverflow
              dataKey="month"
              domain={[left, right]}
              type="number"
            >
              <Label
                value="Relative Months"
                offset={-2}
                position="insideBottom"
              />
            </XAxis>
            <YAxis
              allowDataOverflow
              domain={[bottom, top]}
              type="number"
              yAxisId="1"
              label={{
                value: "Transaction Value",
                angle: -90,
                position: "insideLeft",
                offset: -2,
              }}
            />
            <Tooltip />
            <Legend />
            {/* <ReferenceLine
              xAxisId="0"
              yAxisId="1"
              x={5}
              stroke="red"
              label="Max PV PAGE"
            />
            <ReferenceLine
              yAxisId="1"
              y={5}
              label="Max"
              stroke="red"
              strokeDasharray="3 3"
            /> */}
            {names.map((name, key) => (
              <Line
                key={key}
                yAxisId="1"
                type="stepAfter"
                dataKey={name}
                stroke={colours[key]}
                animationDuration={300}
              />
            ))}
            {refAreaLeft && refAreaRight ? (
              <ReferenceArea
                yAxisId="1"
                x1={refAreaLeft}
                x2={refAreaRight}
                strokeOpacity={0.3}
              />
            ) : null}
          </LineChart>
        </ResponsiveContainer>
      </div>
      <figcaption className={styles.figcaption}>{details}</figcaption>
    </figure>
  )
}

const mapStateToProps = ({
  left,
  right,
  refAreaLeft,
  refAreaRight,
  top,
  bottom,
  animation,
  accounts,
  expenses,
  groupings,
}) => {
  return {
    leftOfChart: left,
    rightOfChart: right,
    refAreaLeftOfChart: refAreaLeft,
    refAreaRightOfChart: refAreaRight,
    topOfChart: top,
    bottomOfChart: bottom,
    animationOfChart: animation,
    accounts,
    expenses,
    groupings,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    newChart: (
      index,
      left,
      right,
      refAreaLeft,
      refAreaRight,
      top,
      bottom,
      animation
    ) =>
      dispatch(
        updateChart(
          index,
          left,
          right,
          refAreaLeft,
          refAreaRight,
          top,
          bottom,
          animation
        )
      ),
  }
}
const ConnectedChart = connect(mapStateToProps, mapDispatchToProps)(Chart)
export default ConnectedChart
