import React from "react"
import styles from "../../styles/budget/tools.module.scss"

const Button = ({ label, colour, handleClick }) => {
  switch (colour) {
    case "green":
      return (
        <button className={styles.greenButton} onClick={handleClick}>
          {label}
        </button>
      )

    case "blue":
      return (
        <button className={styles.blueButton} onClick={handleClick}>
          {label}
        </button>
      )

    case "orange":
      return (
        <button className={styles.orangeButton} onClick={handleClick}>
          {label}
        </button>
      )

    case "red":
      return (
        <button className={styles.redButton} onClick={handleClick}>
          {label}
        </button>
      )

    default:
      return (
        <button className={styles.default} onClick={handleClick}>
          {label}
        </button>
      )
  }
}

export default Button
